/* eslint-disable react/prop-types */
import React from 'react';

import { List, ListItem, ListItemText, makeStyles } from '@material-ui/core';
import { ChevronRight, ExpandMore } from '@material-ui/icons';
import { debug } from 'loglevel';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import TreeMenu, {
  TreeNodeInArray,
  TreeMenuItem,
  Item,
} from 'react-simple-tree-menu';
import { useSubCategory } from 'src/lib/hooks';
import { ROUTE_CONSTANTS } from 'src/routes';

import { AppState } from './../../../state';

const useStyles = makeStyles((theme) => ({
  treeMenuContainer: {
    '&>div': {
      outlineStyle: 'none',
    },
  },
  list: {
    padding: `0px ${theme.spacing(1)}px`,
  },
  listItem: {
    cursor: 'pointer',
    position: 'relative',
    borderRadius: theme.spacing(2),
    WebkitTapHighlightColor: 'transparent',
    '&.level-0': {
      paddingLeft: theme.spacing(2),
    },
    '&.level-1': {
      paddingLeft: theme.spacing(3),
    },
    '&.level-2': {
      paddingLeft: theme.spacing(4),
    },
    '&.on-focus': {
      color: theme.palette.secondary.main,
    },
    '&.is-active': {
      color: theme.palette.primary.main,
    },
  },
  listIcon: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
}));

type CategoryViewerProps = {
  className?: string;
};

// type TreeListItemProps = TreeMenuItem & { onClickItem: (item: Item) => void };
const TreeListItem: React.FC<TreeMenuItem> = ({
  level = 0,
  hasNodes,
  isOpen,
  label,
  toggleNode,
  focused,
  onClick,
  active,
}: TreeMenuItem) => {
  const classes = useStyles();
  const toggleCategory = (ev: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    hasNodes && toggleNode && toggleNode();
    onClick(ev);
  };
  return (
    <ListItem
      onClick={toggleCategory}
      className={`${classes.listItem} level-${level} ${
        focused ? 'on-focus' : ''
      } ${active ? 'is-active' : ''}`}>
      <ListItemText primary={label}></ListItemText>
      {hasNodes && (
        <div className={classes.listIcon}>
          {isOpen ? <ExpandMore /> : <ChevronRight />}
        </div>
      )}
    </ListItem>
  );
};

export const CategoryViewer: React.FC<CategoryViewerProps> = (
  props: CategoryViewerProps
) => {
  const classes = useStyles();
  const history = useHistory();
  const queryParams = new URLSearchParams(history.location.search);
  const chapterQueryParam = queryParams.get('chapter');
  const routeKey = history.location.pathname.replace(
    new RegExp(`${ROUTE_CONSTANTS.LIST_CATEGORY}(\/)?`),
    ''
  );
  const getInitialOpenNodes = () => {
    const nodes = routeKey
      .split('/')
      .map((it) => routeKey.substring(0, routeKey.indexOf(it) + it.length));
    if (nodes.length === 2 && nodes[0] === nodes[1]) {
      nodes[1] = `${nodes[0]}/${nodes[1]}`;
    }
    return nodes;
  };

  const sections = useSelector((st: AppState) => st.kuralIndexMap.section);
  const [, getLiteraturesBySection, getChaptersByLiterature] = useSubCategory();
  const TREE_DATA: TreeNodeInArray[] = Object.values(sections).map(
    (s, sIndex) => ({
      key: s.id.toString(),
      label: s.value,
      index: sIndex,
      nodes: getLiteraturesBySection(s.id).map((lit, litIndex) => ({
        key: lit.id.toString(),
        label: lit.value,
        index: litIndex,
        nodes: getChaptersByLiterature(lit.id).map((ch, chIndex) => ({
          key: ch.id.toString(),
          label: ch.value,
          index: chIndex,
        })),
      })),
    })
  );
  const onItemClick = (item: Item) => {
    const newRoute = `${ROUTE_CONSTANTS.LIST_CATEGORY}/${item.key}`;
    if (!item.hasNodes) {
      const lastItemSeparator = item.key.lastIndexOf('/');
      const chapter = item.key.substring(lastItemSeparator + 1);
      const literatureRoute = item.key.substring(0, lastItemSeparator);
      const currentRoute = `${ROUTE_CONSTANTS.LIST_CATEGORY}/${literatureRoute}?chapter=${chapter}`;
      history.replace(currentRoute);
      history.push(newRoute);
    } else {
      history.replace(newRoute);
    }
  };
  debug('CategoryViewer renders');
  return (
    <div className={classes.treeMenuContainer}>
      <TreeMenu
        initialActiveKey={routeKey}
        initialFocusKey={
          chapterQueryParam ? `${routeKey}/${chapterQueryParam}` : routeKey
        }
        initialOpenNodes={getInitialOpenNodes()}
        onClickItem={onItemClick}
        data={TREE_DATA}
        hasSearch={false}>
        {({ items }) => (
          <List className={classes.list}>
            {items.map(({ key, ...itemProps }) => (
              <TreeListItem key={key} {...itemProps} />
            ))}
          </List>
        )}
      </TreeMenu>
    </div>
  );
};
